<template>
	<div class="its">
		<div class="text_img_show o_h moudle">
			<div class="left_edit fl o_h">
				<img class="fl" :src="info.imgurl">
			</div>
			<div class="right_edit fl">
				<div class="tis_content">
					<div class="tis_content_title">
						{{info.imgtitle}}
					</div>
					<div class="tis_content_count">
						{{info.imgcontent}}
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
export default{
//	name:"its",
	data () {
		return {
			datalist:{}
		}
	},
	props:['info'],
	created:function(){//数据初始化已完成，静态dom已加载
		let self=this;
		this.$post("url",{data:"data"}).then((res)=>{
			if(res.success){
				self.datalist=res.item
			}
		});
	},
	mounted:function(){//所有动态dom初始化挂载完成后
		
	}
}
</script>
<style scoped>
	@import url("../common/css/global.css");
	.text_img_show {
	    background-color: rgb(243,243,243);
	    position: relative;
	}
	.text_img_show .left_edit img{
		width: 100%;
		height:intrinsic;
	    padding: 30px;
	}
	.right_edit,.left_edit {
	    width: 50%;
	    box-sizing: border-box;
	}
	.tis_content {
	    position: absolute;
	    /*text-indent: 30px;*/
	    left: 50%;
	    top: 50%;
	    transform: translateY(-50%);
	} 
	.tis_content_count{
		padding:0 5px;
		word-break: break-all;
	}
	.its{
		padding:0 20%; 
	}
	.tis_content_title{
		background-image: linear-gradient(to right,blue,red);
		-webkit-background-clip: text;
		color: transparent;
		font-weight: 700;
		font-size: 16px;
		text-align: center;
	}
	
	@media screen and (max-width: 767px) {
		.its{
			padding:0;
			margin:.3rem;
			background-color: white;
			border-radius:.06rem;
		}
	}
</style>


