<template>
  <div id="login">
    <div class="login-main-wrapper">
      <ul class="login-list">
        <li :class="index==loginIndex?'active':''" @click="loginList(item,index)" v-for="(item,index) in loginNav" :key="index">
          {{item}}
        </li>
      </ul>
      <div class="login-main" v-show="login">
        <el-form :model="loginForm" ref="loginForm" :rules="rules">
          <el-form-item prop="email">
            <el-input placeholder="邮箱" v-model="loginForm.email">
              <template slot="prepend">邮箱</template>
            </el-input>
          </el-form-item>
          <el-form-item prop="pass">
            <el-input placeholder="密码" type="password" v-model="loginForm.pass" autocomplete="off">
              <template slot="prepend">密码</template>
            </el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" class="login-button" @click="loginBtn('loginForm')" style="width: 100%;background: #42a749;border: none;font-size: .35rem">登录</el-button>
          </el-form-item>
        </el-form>
        <div class="login-foot">
          <router-link to="0">忘记密码</router-link>
          <span @click="loginIndex=1">免费注册</span>
        </div>
      </div>
      <div class="login-main" v-show="register">
        <el-form :model="registerForm" ref="registerForm" :rules="rules">
          <el-form-item prop="email">
            <el-input placeholder="邮箱" v-model="registerForm.email">
              <template slot="prepend">邮箱</template>
            </el-input>
          </el-form-item>
          <el-form-item prop="pass">
            <el-input placeholder="密码" type="password" v-model="registerForm.pass" autocomplete="off">
              <template slot="prepend">密码</template>
            </el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="loginBtn('registerForm')" style="width: 100%;background: #42a749;border: none;font-size: .35rem">注册</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <productFooter></productFooter>
  </div>
</template>

<script type="text/ecmascript-6">
  import productFooter from './footer'
 export default {
    components:{
      productFooter
    },
   data(){
     var validatePass = (rule, value, callback) => {
       if (value === '') {
         callback(new Error('请输入密码'));
       }else {
         callback();
       }
     };
      return{
        loginNav:['登录','注册'],
        login:true,
        register:false,
        loginIndex:0,
        loginEmail:'',
        loginPassword:'',
        loginForm: {
          email:'',
          pass:''
        },
        registerForm: {
          email:'',
          pass:''
        },
        rules: {
          email:[
            {
              required: true,
              message: '请输入邮箱地址',
              trigger: 'blur'
          },
            {
              type: 'email',
              message: '请输入正确的邮箱地址',
              trigger: ['blur', 'change']
            }
          ],
          pass:[
            {
              validator: validatePass, trigger: 'blur'
            }
          ]
        }
      }
   },
   methods:{
      loginList: function (item,index) {
        this.loginIndex = index;
        if (index == 0){
          this.login = true;
          this.register = false;
        } else {
          this.login = false;
          this.register = true;
        }
      },
     loginBtn: function (login) {
       this.$refs[login].validate((valid) => {
         if (valid) {
           alert('submit!');
         } else {
           console.log('error submit!!');
           return false;
         }
       });
     }
   }
 }
</script>

<style lang="stylus" scoped rel="stylesheet/stylus">
  @import "../common/stylus/general.styl"
  .login-main-wrapper
    width 500px
    height 400px
    border 1px solid #ddd
    margin-top 100px
    .login-list
      width 100%
      height 50px
      margin-bottom 50px
      li
        float left
        height 100%
        width 50%
        box-sizing border-box
        background #eee
        border 1px solid #ddd
        cursor pointer
        text-align center
        font-size fl
        color #666
        line-height 50px
        font-weight bold
      li.active
        background #fff
        color color
        border none
  .login-main
    width 80%
    margin 0 auto
  .login-btn>button
    width 100%
    background color
    font-size fl
    color #fff
    height 40px
    line-height 40px
    text-align center
    border-radius 4px
    outline none
    border none
    cursor pointer
  .login-btn>button:hover
    opacity 0.9
  .login-foot
    font-size fm
    margin-top 50px
    text-align center
    span
      color #666
      margin-left 50px
      cursor pointer
  #footer
    position fixed
    bottom 0
    left 0
    right 0
</style>



