<template>
  <div id="mall-header">
    <div class="top-header hideBox">
      <div class="mall-header-main container">
        <div class="mall-header-main-left fl">
          <span>欢迎来到商城</span>
        </div>
        <div class="mall-header-main-right fr">
          <ul>
            <li v-for="item in headerList">
              <router-link :to="item.url">{{item.message}}</router-link>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="search-header container hideBox">
      <div class="logo-img">
        <img src="../common/img/logo.jpg" alt="" width="100" height="80">
      </div>
      <div class="search-input">
        <input placeholder="请输入内容" v-model="search" class="search-box"><el-button type="success" icon="el-icon-search" class="search-btn">搜索</el-button>
      </div>
      <div class="shop-cat">
        购物车(<span>0</span>)
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data(){
      return{
        headerList: [
          {message: '登录',url: '/productList'},
          {message: '注册',url: '2'},
          {message: '售后',url: '3'},
          {message: '个人中心',url: '4'},
          {message: '消息通知',url: '5'},
        ],
        search: ""
      }
    }
  }
</script>

<style lang="stylus" scoped rel="stylesheet/stylus">
@import "../common/stylus/general.styl"
  #mall-header
    border-bottom 1px solid #e2e2e2
  .top-header
    background #f5f5f5
    height 35px
    line-height 35px
    border-bottom: 1px solid #eee
  .mall-header-main
    background #f5f5f5
    span
      color #666
      font-size fm
    .mall-header-main-right li
      display inline-block
      margin-right 20px
      font-size fs
    .mall-header-main-right li:last-child
      margin 0
  .search-header
    padding 20px 0
    height 80px
    line-height 80px
    text-align center
    position relative
    .logo-img
      position absolute
      left 0
    .search-input
      width 50%
      .search-box
        width 80%
        border 1px solid color
        height 40px
        line-height 40px
        border-radius 4px 0 0 4px
        padding-left 10px
        outline none
        box-sizing border-box
      .el-button
        background color
        border-color color
        border-radius 0 4px 4px 0
        width 17%
        padding 0
        height 40px
      .el-button:hover
         opacity 0.9
    .shop-cat
      position absolute
      right 0
      top 45px
      border 1px solid #ddd
      background #efefef
      height 30px
      line-height 30px
      padding 0 15px
      cursor pointer
      color #666
@media screen and (max-width: 767px){
  .hideBox{
    display none
  }
}
</style>



