<template>
  <div id="footer">
    <div id="mall-footer" class="hideBox">
      <div class="mall-footer-main container">
        <div class="clearFloat">
          <ul>
            <li v-for="list in list1">
              <router-link :to="list.url">{{list.message}}</router-link>
            </li>
          </ul>
          <ul>
            <li v-for="list in list2">
              <router-link :to="list.url">{{list.message}}</router-link>
            </li>
          </ul>
          <ul>
            <li v-for="list in list3">
              <router-link :to="list.url">{{list.message}}</router-link>
            </li>
          </ul>
          <ul>
            <li v-for="list in list4">
              <router-link :to="list.url">{{list.message}}</router-link>
            </li>
          </ul>
          <div class="ewm-img">
            <ul>
              <li>
                <img src="../common/img/logo.jpg" alt="">
                <p>扫码优惠</p>
              </li>
              <li>
                <img src="../common/img/logo.jpg" alt="">
                <p>扫码优惠</p>
              </li>
            </ul>
          </div>
        </div>
        <div class="footer-text">
        <span>
          &copy2016（公司名称）版权所有，粤ICP备15063842号
        </span>
        </div>
      </div>
    </div>
    <div id="phone-footer">
      <ul>
        <li v-for="nav in footNav" :class="{active: activeName == nav.url}" class="nav-list" :data-url="nav.url">
          <router-link :to="nav.url">
            <i :class="nav.icon"></i>
            <p>{{nav.name}}</p>
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data() {
      return {
        activeName: '',
        list1: [
          {message: '登录', url: '/productList'},
          {message: '注册', url: '2'},
          {message: '售后', url: '3'},
          {message: '个人中心', url: '4'},
          {message: '消息通知', url: '5'},
        ],
        list2: [
          {message: '登录', url: '/productList'},
          {message: '注册', url: '2'},
          {message: '售后', url: '3'},
          {message: '个人中心', url: '4'},
          {message: '消息通知', url: '5'},
        ],
        list3: [
          {message: '登录', url: '/productList'},
          {message: '注册', url: '2'},
          {message: '售后', url: '3'},
          {message: '个人中心', url: '4'},
          {message: '消息通知', url: '5'},
        ],
        list4: [
          {message: '登录', url: '/productList'},
          {message: '注册', url: '2'},
          {message: '售后', url: '3'},
          {message: '个人中心', url: '4'},
          {message: '消息通知', url: '5'},
        ],
        footNav : [
          {'name': '首页',url: '/index','icon': 'iconfont icon-third-home'}
          ,{'name': '列表',url: '/productList','icon': 'iconfont icon-third-category'}
          ,{'name': '购物车',url: '/shopCart','icon': 'iconfont icon-third-shoppingcart'}
          ,{'name': '个人中心',url: '/userCenter','icon': 'iconfont icon-third-mine'}
        ]
      }
    },
    mounted (){
      this.navClick();
    },
    methods: {
      navClick: function () {
        let href = window.location.href;
        let nav = document.querySelectorAll('.nav-list');
        let _this = this;
        nav.forEach(function (item) {
         let str = item.getAttribute("data-url");
          if (href.indexOf(str) != -1){
            _this.activeName = str;
          }
        });
      }
    }
  };
</script>

<style lang="stylus" scoped rel="stylesheet/stylus">
  @import "../common/stylus/general.styl"
  #phone-footer
    display none
  #mall-footer
    height 300px
    background #999
    .mall-footer-main
      background #999
      padding-top 50px
      ul
        float left
        margin-right 6%
        li
          margin-bottom 10px
          a
            color #eee
            font-size 16px
      .ewm-img
        float right
        width 25%
        ul
          width 100%
          margin-right 0
          float right
          text-align right
          li
            display inline-block
            text-align center
            color #eee
            margin-right 5% !important
            width 42%
            img
              width 100%
              margin-bottom 10px
      .footer-text{
        text-align center
        color #333
        margin-top 50px
      }
  @media screen and (max-width: 767px){
    .hideBox{
      display none
    }
    #phone-footer{
      display block !important
      z-index 2019
      height 1.3rem
      position fixed
      left 0
      right 0
      bottom 0
      background #Fff
      border-top 1px solid #ddd
    }
    #phone-footer ul{
      display -webkit-box
      box-sizing border-box
      overflow hidden
      width 100%
      height 1.3rem
    }
    #phone-footer ul li{
      position relative
      text-align center
      margin-top .15rem !important
      color #666
      width 100%
      box-sizing border-box
      -webkit-box-flex 1
      font-size .36rem
    }
    #phone-footer ul li.active a{
      color color
    }
    #phone-footer ul li i{
      font-size .5rem
    }
  }
</style>



