<template>
  <div id="shopCart">
    <productHeader></productHeader>
    <div class="cart-header">购物车</div>
    <div class="shop-cart-wrap container">
      <div class="cart-item box">
        <div class="cart-item-head">
          <ul>
            <li>商品信息</li>
            <li>单价</li>
            <li>数量</li>
            <li>金额</li>
            <li>操作</li>
          </ul>
        </div>
        <ul class="cart-item-list">
          <!--小屏下-->
          <li v-for="(item,index) in productList" class="phone-cart">
            <div class="cart-message">
              <div class="cart-item-check">
                <a href="javascript:void 0" class="item-check-btn" :class="{check:item.checked}" @click="checkProduct(item)">
                </a>
              </div>
              <div class="cart-item-pic">
                <img :src="item.imgPic" :alt="item.name">
              </div>
              <div class="fl product-name">
                <div class="item-name">{{item.name}}</div>
              </div>
              <div class="fl product-gift">
                <dl>
                  <dd v-for="gift in item.gifts" class="fl">{{gift.partName}}</dd>
                </dl>
              </div>
              <div class="item-price fl">{{item.price | money}}</div>
            </div>
            <div class="cart-fun">
              <div class="cart-tab">
                <div class="item-quantity">
                  <div class="select-self select-self-open">
                    <div class="quantity">
                      <el-input-number size="mini" :min="1" v-model="item.count" @change="handleChange()"></el-input-number>
                    </div>
                  </div>
                </div>
              </div>
              <div class="cart-tab">
                <div class="item-price-total">{{item.price*item.count | money}}</div>
              </div>
              <div class="cart-tab">
                <div class="cart-item-operation">
                  <el-button type="danger" @click="deleteProduct(item)" size="mini">删除</el-button>
                </div>
              </div>
            </div>
          </li>
          <li class="pc-cart" v-for="(item,index) in productList">
            <div class="cart-tab-1">
              <div class="cart-item-check">
                <a href="javascript:void 0" class="item-check-btn" :class="{check:item.checked}" @click="checkProduct(item)">
                </a>
              </div>
              <div class="cart-item-pic">
                <img :src="item.imgPic" :alt="item.name">
              </div>
              <div class="cart-item-title">
                <div class="item-name">{{item.name}}</div>
              </div>
              <div class="item-include">
                <dl>
                  <dt>赠送:</dt>
                  <dd v-for="gift in item.gifts">{{gift.partName}}</dd>
                </dl>
              </div>
            </div>
            <div class="cart-tab-2">
              <div class="item-price">{{item.price | money}}</div>
            </div>
            <div class="cart-tab-3">
              <div class="item-quantity">
                <div class="select-self select-self-open">
                  <div class="quantity">
                    <el-input-number size="mini" :min="1" v-model="item.count" @change="handleChange()"></el-input-number>
                  </div>
                </div>
              </div>
            </div>
            <div class="cart-tab-4">
              <div class="item-price-total">{{item.price*item.count | money}}</div>
            </div>
            <div class="cart-tab-5">
              <div class="cart-item-operation">
                <el-button type="danger" @click="deleteProduct(item)">删除</el-button>
              </div>
            </div>
          </li>
        </ul>
      </div>
      <div class="cart-foot-wrap">
        <div class="cart-foot-l">
          <div class="item-all-check">
            <a href="javascript:void 0" @click="checkAll">
              <span class="item-check-btn" :class="{check:isChecked}"></span>
              <span>全选</span>
            </a>
          </div>
          <div class="item-all-del">
            <el-button size="mini" type="danger" @click="deleteChecked">删除</el-button>
          </div>
          <div class="item-all-del selected-num">
            已选商品
            <span>{{selectProductNum}}</span>
            件
          </div>
        </div>
        <div class="cart-foot-r">
          <div class="item-total">
            合计 <span class="ls-hide">（不含运费）</span>: <span class="total-price">{{totalMoney | money}}</span>
          </div>
          <div class="next-btn-wrap">
            <a href="javascript:void(0)" class="btn btn--red" @click="account">结算</a>
          </div>
        </div>
      </div>
    </div>
    <productFooter></productFooter>
    <goTop></goTop>
  </div>
</template>

<script type="text/ecmascript-6">
  import productHeader from './header'
  import productFooter from './footer'
  import goTop from './goTop'
  import http from '../common/js/http'
  export default {
    components: {
      productHeader,
      productFooter,
      goTop
    },
    data (){
      return {
        productList: [],//数据
        totalMoney: 0,//总额
        selectProductNum: 0,//选中的个数
        isChecked: false,//判定全选
        curProduct: '',//删除当前的数据
        selectProduct:[],//删除选中的数据
      }
    },
    mounted () {
      this.cartView();
      this.calcTotalPrice()
    },
    methods: {
      //数字计算器，重新计算总额
      handleChange: function () {
        this.calcTotalPrice();
      },
      //数据存放
      cartView: function () {
        let _this = this;
        // this.$fetch('http://localhost:8080/static/data.json').then((res) => {
        //   console.log(res.result)
        //   _this.productList = res.result.list
        // })
        this.$fetch('/indexapi/index').then((res) => {
        // this.$fetch('http://127.0.0.1:8087/indexapi/index').then((res) => {
        //   console.log(res)
          // _this.productList = res.result.list
        })
      },
      //选择商品按钮，同时改变选中数量，以及将选中的数据进行另外存放，并重新计算总额
      checkProduct: function(item){
        if(typeof item.checked == 'undefined'){
          this.$set(item,'checked',true)
        }else {
          item.checked = !item.checked;
        }
        if(item.checked){
          this.selectProductNum++;
          this.selectProduct.push(item)
        }else {
          this.selectProductNum--;
          let index = this.selectProduct.indexOf(item);
          this.selectProduct.splice(index,1)
        }
        this.calcTotalPrice();//重新计算总额
        this.isChecked = this.productList.every(function(item){
          return item.checked === true;//判定时需要使用恒等判定，如果只是‘=’判定，结果会始终返回true。
        });
      },
      //选择全选，同时改变选中产品数量值以及选中的数据，重新计算总额
      checkAll: function(){
        this.isChecked = !this.isChecked;
        let _this = this;
        this.productList.forEach(function (item,index) {
          if(typeof item.checked == 'undefined'){
            _this.$set(item,'checked',_this.isChecked)
          }else {
            item.checked = _this.isChecked
          }
        });
        if (this.isChecked){
          this.selectProductNum = this.productList.length;
          this.selectProduct = [];
          this.selectProduct = this.selectProduct.concat(this.productList);
        } else {
          this.selectProductNum = 0;
          this.selectProduct = []
        }
        this.calcTotalPrice();
      },
      //封装计算总额
      calcTotalPrice: function () {
        this.totalMoney = 0;
        let _this = this;
        this.productList.forEach(function(item,index){
          if(item.checked){
            _this.totalMoney += item.count*item.price;
          }
        });
      },
      //删除单个商品按钮，同时改变选中数量，改变按钮高亮
      deleteProduct: function (item) {
        this.curProduct = item;
        let _this = this;
        this.$confirm('确定删除该商品吗？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let index = _this.productList.indexOf(_this.curProduct);
          _this.productList.splice(index,1);
          _this.calcTotalPrice();
          if(_this.productList.length == 0){
            _this.isChecked = false
          }
          if (item.checked){
            _this.selectProductNum--
          }
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      //删除选中的按钮
      deleteChecked: function () {
        let _this = this;
        if(this.selectProductNum > 0){
          this.$confirm('确定删除选中的商品吗？', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.isChecked = false;
            this.selectProductNum = 0;
            _this.selectProduct.forEach(function (item) {
              let index = _this.productList.indexOf(item);
              _this.productList.splice(index,1);
              _this.calcTotalPrice()
            });
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        }else {
          this.$message.error('请选择要删除的商品！');
        }
      },
      //结算按钮
      account: function () {
        if (this.selectProductNum <= 0){
          this.$message.error('请选择商品结算！')
        } else {
          return
        }
      }
    }
  }
</script>

<style lang="stylus" scoped rel="stylesheet/stylus">
  @import "../common/stylus/general.styl"
  *{transition: all .1s ease-in;}
  .cart-header,.phone-cart
    display none !important
  .shop-cart-wrap
    margin-top 0.25rem
    margin-bottom 0.25rem
  .cart-item
    display table
    width 100%
    border 1px solid #ddd
    box-sizing border-box
  .cart-item-head
    display table-header-group;
    width 100%
  .cart-item-head ul
    display table-row
    width 100%
  .cart-item-head li
    display table-cell
    height 0.3rem
    line-height .9rem
    background #ddd
    color #666
    font-size fm
    text-align center
  .cart-item-head li:nth-child(2), .cart-item-head li:nth-child(3), .cart-item-head li:nth-child(4), .cart-item-head li:nth-child(5)
    width 14%
    padding 0 10px
  .cart-item-list
    display table-row-group
  .cart-item-list > li
    position relative
    display table-row
    padding .5rem 0
    border-bottom 1px solid #ddd
  .cart-item-list > li:last-child
    border none
  .cart-item-list > li > div
    position relative
    display table-cell
    text-align center
    vertical-align top
  .cart-item-list > li.disabled > div:after
    position absolute
    top 0
    left 0
    content ""
    width 100%
    height 100%
    background rgba(96, 95, 95, 0.3)
    z-index 5
  .cart-item-list .cart-tab-1
    min-height 1rem
    padding .5rem 0
    text-align left
  .cart-item-list .cart-tab-2
    padding-top 74px
  .cart-item-list .cart-tab-3
    padding-top 69px
  .cart-item-list .cart-tab-4
    padding-top 74px
  .cart-item-list .cart-tab-5
    padding-top 64px
  .cart-item-list .cart-item-check
    float left
    padding 28px 0 28px 22px
  .cart-item-list .cart-item-pic
    float left
    width 100px
    height 100px
    margin-left 22px
  .cart-item-list .cart-item-pic img
    width 100%
    height 100%
    cursor:pointer
  .cart-item-list .cart-item-title
    min-height 68px
    padding 0 20px 0 170px
  .cart-item-list .cart-item-title .item-name
    margin 2px 0 10px
    line-height 16px
    color #000
    font-size 14px
  .cart-item-list .item-include
    position relative
    padding-left 170px
  .cart-item-list .item-include dl dt
    float left
  .cart-item-list .item-include dl dd
    padding-left 40px
    color #666
  .cart-item-list .item-include dl:after
    visibility hidden
    display block
    content " "
    clear both
  .item-check-btn
    display inline-block
    width 16px
    height 16px
    border 1px solid #ccc
    border-radius 3px
    text-align center
    vertical-align middle
    cursor pointer
    transition: background-color .25s;
  .item-check-btn:hover
    border-color color
  .item-check-btn.check
    background color
    border-color color
  .item-check-btn.check:after
    content ""
    display block
    border 0 solid #fff
    border-width 0 0 2px 2px
    transform rotate(-45deg)
    position absolute
    top 4px
    height 4px
    width 8px
  .cart-foot-wrap
    line-height .9rem
    margin-top .3rem
    background #eee
  .cart-foot-wrap .cart-foot-l
    float left
    font-size fm
    padding 0 .3rem
    width 40%
  .cart-foot-wrap .item-all-check
    float left
    color #EE7A23
  .cart-foot-wrap .item-all-check .item-check-btn
    margin-right 5px
  .cart-foot-wrap .item-all-check span
    vertical-align middle
  .cart-foot-wrap .item-all-del
    float left
    margin-left .4rem
    text-align center
    color #666
  .cart-foot-wrap .item-all-del span
    color color
  .cart-foot-wrap .cart-foot-r
    float right
  .cart-foot-wrap .item-total
    float left
    margin-right 31px
    color #605F5F
    font-size fm
  .cart-foot-wrap .item-total .total-price
    color color
    font-family "moderat", sans-serif
    font-size fl
  .cart-foot-wrap .next-btn-wrap
    float right
    width 150px
    line-height .9rem
    text-align center
    background color
  .cart-foot-wrap .next-btn-wrap a
    display block
    color #fff
    font-size fxl
  .cart-foot-wrap:after
    visibility hidden
    display block
    content " "
    clear both
  .item-price,.item-price-total
    color color
    font-size fm
  /* 小于767px */
  @media screen and (max-width: 767px){
    .container{
      width 100%
      padding-bottom 2.6rem
    }
    .cart-item-head{
      display none !important
    }
    .box{
      display block !important
    }
    .cart-header{
      display block !important;
      font-size fxl
      color #fff
      background color
      line-height 1.2rem
      text-align center
    }
    .selected-num,.pc-cart{
      display none !important
    }
    .ls-hide{
      position: absolute;
      bottom: -0.1rem;
      right: 0;
      height: 20px;
      line-height: 20px;
      font-size fs
    }
    .cart-foot-wrap .next-btn-wrap{
      width 2rem
      height 1.3rem
      line-height 1.3rem
    }
    .cart-foot-wrap .cart-foot-l{
      width auto
    }
    .cart-foot-wrap .item-total{
      margin-right .2rem
      position relative
      line-height 1.1rem
      font-size fxl
    }
    .cart-foot-wrap{
      height 1.3rem
      line-height 1.3rem
      position fixed
      bottom 1.3rem
      left 0
      right 0
    }
    .cart-item-list{
      display block
      margin-top 0.2rem
    }
    .cart-item-list > li,.cart-item-list > li > div{
      display block
      overflow hidden
    }
    .cart-item-check{
      margin-top 1.2rem
      margin-left 5%
      padding 0 !important
    }
    .cart-item-list .cart-item-pic{
      width 3rem
      height 3rem
      margin-left 5%
      margin-right .2rem
    }
    .product-name,.product-gift{
      width 52%
      text-align left
      font-size fxl
      margin-bottom  .2rem
    }
    .product-gift dd{
      color #999
      font-size fs
    }
    .item-price{
      font-size fxl
    }
    .cart-fun{
      height 28px
      line-height 28px
      margin-top .5rem
      margin-left 5%
    }
    .cart-tab{
      float left
      margin-right .2rem
    }
    .cart-item{
      border none
    }
    .el-message-box{
      width 70% !important
    }
    .phone-cart{
      display block !important
    }
    .item-price-total,.total-price{
      font-size fxxl !important
    }
  }
</style>



