<template>
  <div id="productDetail">
    <productHeader></productHeader>
    <div class="product-detail-main clearFloat container">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{path: '/productList'}">产品列表</el-breadcrumb-item>
        <el-breadcrumb-item>产品详情</el-breadcrumb-item>
      </el-breadcrumb>
      <div class="product-detail-wrap clearFloat">
        <div class="product-detail-img fl" v-if="type === 'pc'">
          <div class="pic-zoom">
            <pic-zoom :url="url" :scale="3" :big-url="url" :scroll="true"></pic-zoom>
          </div>
          <div class="small-img clearFloat">
            <i class="el-icon-arrow-left arrow-left fl" @click="imgScroll('left')"></i>
            <div class="small-img-wrap fl">
              <ul class="scroll-wrap">
                <li v-for="item in urlList">
                  <img :src="item" alt="" @mousemove="mouseover(item)" :class="{srcActive:srcActive==item}">
                </li>
              </ul>
            </div>
            <i class="el-icon-arrow-right arrow-right fr" @click="imgScroll('right')"></i>
          </div>
        </div>
        <div class="phone-banner" v-else-if="type === 'phone'">
          <wc-swiper>
            <wc-slide v-for="item in urlList" :key="item">
              <img :src="item" alt="">
            </wc-slide>
          </wc-swiper>
        </div>
        <div class="fr product-detail">
          <h5 class="product-detail-title mgb20">哈哈哈哈或或或或或或或或或</h5>
          <div class="product-detail-price mgb20">
            <span class="price-text">价格：</span>
            <span class="price">¥ 163</span>
          </div>
          <div class="mgb20">
            <span class="same-title">配送至：</span>
            <span class="same-title">北京朝阳</span>
            <span class="same-title"> > 有货 </span>
          </div>
          <div class="mgb20">
            <span class="same-title">重量：</span>
            <span class="same-title">2KG</span>
          </div>
          <div class="select-parameter mgb20 clearFloat">
            <span class="same-title fl ">
              选择颜色：
            </span>
            <ul class="clearFloat">
              <li v-for="item in wpList" :key="item.name" :class="{active : active == item.name}" @click="selected('parameter',item.name)">{{item.name}}</li>
            </ul>
          </div>
          <div class="mgb20">
            <span class="same-title fl">选择数量：</span>
            <div>
              <el-input-number v-model="num" :min="1" :max="10" size="mini" label="描述文字"></el-input-number>
            </div>
          </div>
          <div class="btn-wrap">
            <button class="buy-btn">立即购买</button>
            <button class="cat-btn">加入购物车</button>
          </div>
        </div>
      </div>
      <div class="product-introduce">
        <div class="product-introduce-title">
          <ul class="clearFloat">
            <li v-for="item in introduceList" :class="{active:active1==item.name}" :key="item.name" @click="selected('introduce',item.name)">
              {{item.name}}
            </li>
          </ul>
        </div>
      </div>
    </div>
    <goTop></goTop>
    <productFooter></productFooter>
  </div>
</template>

<script type="text/ecmascript-6">
  import PicZoom from 'vue-piczoom'
  import productHeader from './header'
  import productFooter from './footer'
  import goTop from './goTop'
  export default {
    components: {
      productHeader,
      productFooter,
      goTop,
      PicZoom
    },
    data() {
      return {
        active:'',
        active1:'商品介绍',
        srcActive :'',
        num: 1,
        tempLength : 0,//临时变量,当前移动的长度
        type: '',
        configs: {
          width:400,
          height:400,
          maskWidth:100,
          maskHeight:100,
          maskColor:'#ddd',
          maskOpacity:0.5
        },
        introduceList:[{name:'商品介绍'},{name:'商品评价（100）'}],
        wpList: [
          {name: '食品饮料'},
          {name: '鲜花'},
          {name: '蛋糕'},
          {name: '水果生鲜'},
          {name: '服装鞋帽'},
          {name: '其它'}
        ],
          // bigUrl: 'http://imgsrc.baidu.com/imgad/pic/item/3801213fb80e7beca9004ec5252eb9389b506b38.jpg',
          url:'https://826327700.github.io/vue-piczoom/dist/static/imac2.jpg',
          urlList:['http://imgsrc.baidu.com/imgad/pic/item/3801213fb80e7beca9004ec5252eb9389b506b38.jpg','https://826327700.github.io/vue-piczoom/dist/static/imac2.jpg',]
      }
    },
    mounted (){
      this.screenWidth();
    },
    methods: {
      mouseover: function (imgSrc) {
        this.url = imgSrc;
        this.srcActive = imgSrc
      },
      screenWidth: function() {
        if(document.body.clientWidth <= 767){
          this.type = 'phone'
          console.log(this.type)
        }else {
          this.type = 'pc'
        }
      },
      selected(type,name){
        if (type == "parameter") {
          this.active = name;
        }else {
          this.active1 = name;
        }
      },
      imgScroll: function (type) {
        let viewNum = 5; //设置每次显示图片的个数量
        let moveNum = 2; //每次移动的数量
        let scrollDiv = document.querySelector(".scroll-wrap"); //进行移动动画的容器
        let scrollItems = document.querySelectorAll(".small-img-wrap ul li"); //移动容器里的集合
        let moveLength = 87 * moveNum; //计算每次移动的长度
        let countLength = (scrollItems.length - viewNum) * 87; //计算隐藏li的长度
        let moveLeft = Number(window.getComputedStyle(scrollDiv).left.slice(0,-2));//容器的left值
        if(type == 'right'){
          if(this.tempLength < countLength){
            if((countLength - this.tempLength) > moveLength){
              scrollDiv.style.left = `${moveLeft - moveLength}px`;
              this.tempLength += moveLength;
            }else{
              scrollDiv.style.left = `${moveLeft - (countLength - this.tempLength)}px`;
              this.tempLength = countLength;
            }
          }
        }else {
          if(this.tempLength > 0){
            if(this.tempLength > moveLength){
              scrollDiv.style.left = `${moveLength + moveLeft}px`;
              this.tempLength -= moveLength;
            }else{
              scrollDiv.style.left = `${this.tempLength + moveLeft}px`;
              this.tempLength = 0;
            }
          }
        }
      }
    }
  };
</script>

<style lang="stylus" scoped rel="stylesheet/stylus">
  @import "../common/stylus/general.styl"
  .product-detail-main
    margin-top 20px
    margin-bottom 20px
  .active
    background: #fd7522
    border: 1px solid #fd7522
    color: #fff
  .product-detail-wrap
    margin-top 20px
    position relative
  .magnifier-box img
    height 100%
  .pic-zoom
    width 400px
    height 400px
    border 1px solid #ddd
  .small-img
    width 400px
    position relative
    margin-top 15px
  .small-img-wrap
    width 340px
    overflow hidden
    ul
      width 10000px
      overflow hidden
      position relative
      left 0
      transition all .3s ease-in
      li
        float left
        margin-right 10px
        img
          width 77px
          height 77px
          cursor pointer
          box-sizing border-box
        .srcActive
          border 1px solid color
  .arrow-left,.arrow-right
    display block
    font-size 30px
    color color
    height 80px
    line-height 80px
    cursor pointer
  .product-detail,.product-introduce
    transition all .2s ease-in
  .product-detail-title
    color #333
    font-size 16px
    margin-top 10px
  .product-detail-price
    padding 5px 15px
    background #ddd
    border-radius 2px
    font-size 16px
  .price-text
    color #333
  .price
    color color
  .same-title
    color #666
    font-size 16px
    display inline-block
    width 100px
    text-align left
  .select-parameter li
    float left
    margin-right 3%
    padding 2% 3%
    font-size 14px
    color #666
    border 1px solid #ddd
    margin-bottom 10px
    cursor pointer
    border-radius 5px
  .select-parameter ul
    width 68%
    float left
  .select-parameter span
    margin-top 10px
  .select-parameter li.active
    background color
    color #fff
    border-color color
  .buy-btn,.cat-btn
    padding .2rem .3rem
    font-size fl
    border none
    outline none
    cursor pointer
    margin-top 20px
    border-radius 2px
  .buy-btn
    background color
    color #fff
    margin-right 20px
  .cat-btn
    background #ddd
    color #333
  .product-introduce-title
    background #ddd
    margin-top 50px
    li
      font-size fl
      padding .2rem .3rem
      cursor pointer
      color #333
      float left
      border none
    li.active
      background color
      color #fff
  /* 小于767px */
  @media screen and (max-width: 767px){
    .product-detail-main{
      padding-bottom 1.3rem
    }
    .btn-wrap{
      text-align center
    }
    .product-detail-price{
      padding-left 0
    }
    .phone-banner img{
      width 100%
    }
    .product-detail-img{
      display none
    }
    .product-detail{
      width 100%
    }
  }
</style>



