<template>
	<div :class="['bie_img_edit moudle mid tmiddle',allinfo.jsons.foote]" :style=" 'background-image:url('+info.imgurl+')' ">
		<div class="bie_content banner_middle_center">
			<div class="bie_title">{{info.imgtitle}}</div>
			<div class="bie_cont">{{info.imgcontent}}</div>
		</div>
	</div>
</template>

<script>
	import globa from "../common/js/alljson.js"
	export default{
		name:"big_img",
		props:['info'],
		comcomponents:{globa},
		data () {
			return {
				allinfo:globa
			}
		},
		mounted () {
			$(".bie_img_edit").css({
				"backgroundImage":"url(https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=3109205727,2642555430&fm=27&gp=0.jpg)"
			});
		}
	}
	
</script>

<style scoped>
	@import url("../common/css/global.css");
	.bie_img_edit {
	    width: 100%;
	    position: relative;
	    background: url(/img/touxiang.jpeg) no-repeat;
	    background-size: cover;
	    margin: 20px 0;
	    text-align: center;
	}
	
	
	.bie_title {
	    font-size: 36px;
	    margin-bottom: 20px;
	    font-weight: 700;
	    overflow: hidden;
	    text-overflow: ellipsis;
	    white-space: nowrap;
	}
	
	.bie_content {
	    position: absolute;
	    color: rgba(255,255,255,.7);
	    max-width: 60%;
	    text-shadow: 0 0 15px rgb(77,77,77);
	}
	
	
	.bie_img_edit.sosmall{
		padding-bottom:30%;
	}
	.bie_img_edit.small{
		padding-bottom:40%;
	}
	.bie_img_edit.mid{
		padding-bottom:50%;
	}
	.bie_img_edit.big{
		padding-bottom:60%;
	}
	.bie_img_edit.supperbig{
		padding-bottom:70%;
	}
	.bie_img_edit.ttop{
		background-position: top;
	}
	.bie_img_edit.tmiddle{
		background-position:center;
	}
	.bie_img_edit.tbottom{
		background-position: bottom;
	}
</style>


