import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import productList from '@/components/productList'
import productDetail from '@/components/productDetail'
import shopCart from '@/components/shopCart'
import activity from '@/components/activity'
import userCenter from '@/components/userCenter'
import swiperShow from '@/components/swiperShow'
import img_text_stairs from '@/components/img_text_stairs'
import big_img from '@/components/big_img'
import edit_list from '@/components/edit_list'
import orderAddress from '@/components/orderAddress'
import login from '@/components/login'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/index',
      name: 'index',
      component: index,
      meta: {
        title: '商城首页'
      }
    },
    {
      path: '/edit_list',
      name: 'edit_list',
      component: edit_list,
      meta: {
        title: '编辑系列'
      }
    },
    {
      path: '/swiperShow',
      name: 'swiperShow',
      component: swiperShow,
      meta: {
        title: '哦呦呦轮播图呢'
      }
    },
     {
      path: '/img_text_stairs',
      name: 'img_text_stairs',
      component: img_text_stairs,
      meta: {
        title: '哦呦呦图片+文本呢'
      }
    },
    {
      path: '/big_img',
      name: 'big_img',
      component: big_img,
      meta: {
        title: '哦呦呦大图片呢'
      }
    },
    {
      path: '/productList',
      name: 'productList',
      component: productList,
      meta: {
        title: '商品列表'
      }
    },
    {
      path: '/productDetail',
      name: 'productDetail',
      component: productDetail,
      meta: {
        title: '商品详情'
      }
    },
    {
      path: '/shopCart',
      name: 'shopCart',
      component: shopCart,
      meta: {
        title: '购物车'
      }
    },
    {
      path: '/orderAddress',
      name: 'orderAddress',
      component: orderAddress,
      meta: {
        title: '订单地址'
      }
    },
    {
      path: '/activity',
      name: 'activity',
      component: activity,
      meta: {
        title: '活动页'
      }
    },
    {
      path: '/login',
      name: 'login',
      component: login,
      meta: {
        title: '登录注册'
      }
    },
    {
      path: '/userCenter',
      name: 'userCenter',
      component: userCenter,
      meta: {
        title: '个人中心'
      }
    }
  ]
})




