<template>
  <div id="orderAddress">
    <productHeader></productHeader>
    <div class="cart-header">订单详情</div>
    <div class="shop-cart-wrap container">
      <div class="user-address" v-show="formShow">
        <el-form ref="formInline" :model="formInline" :rules="rules" label-width="100px" class="demo-ruleForm">
          <el-form-item label="国家" prop="country">
            <el-button @click="dialogFormVisible = true">{{formInline.country}}</el-button>
          </el-form-item>
          <el-form-item label="姓名" prop="name">
            <el-input type="text" placeholder="请输入姓名" v-model="formInline.name" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="手机号码" prop="phone">
            <el-input v-model.number="formInline.phone" placeholder="请输入手机号码" type="text" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="选择城市" prop="city">
            <div class="select-list">
              <el-select
                filterable
                v-model="formInline.city"
                @change="choseProvince"
                placeholder="省级地区">
                <el-option
                  v-for="item in province"
                  :key="item.id"
                  :label="item.value"
                  :value="item.id">
                </el-option>
              </el-select>
              <el-select
                filterable
                v-model="cityValue"
                @change="choseCity"
                placeholder="市级地区">
                <el-option
                  v-for="item in cityArr"
                  :key="item.id"
                  :label="item.value"
                  :value="item.id">
                </el-option>
              </el-select>
              <el-select
                filterable
                v-model="areaValue"
                @change="choseBlock"
                placeholder="区级地区">
                <el-option
                  v-for="item in areaArr"
                  :key="item.id"
                  :label="item.value"
                  :value="item.id">
                </el-option>
              </el-select>
            </div>
          </el-form-item>
          <el-form-item label="详细地址" prop="address">
            <el-input type="text" v-model="formInline.address" placeholder="请输入详细地址" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="邮箱地址" prop="email">
            <el-input type="email" v-model="formInline.email" placeholder="请输入邮箱地址" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="邮编" prop="postCode">
            <el-input v-model="formInline.postCode" placeholder="请输入邮编" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('formInline')">保存</el-button>
            <el-button @click="resetForm('formInline')">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <el-dialog title="确定更换国家？页面将跳转对应的国家链接。" :visible.sync="dialogFormVisible">
        <el-select placeholder="请选择国家" v-model="formInline.country">
          <el-option v-for="item in selectCountry" :key="item.id" :value="item.name">
            <router-link :to="item.url" class="country-a">{{item.country}}</router-link>
          </el-option>
        </el-select>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
        </div>
      </el-dialog>
      <div class="address-list" v-show="addressShow">
        <ul class="clearFloat">
          <li class="address-li">
            <div class="address-ss">
              <span>广东省深圳市（<span>董红卫</span>收）</span>
              <div class="address-default">默认</div>
            </div>
            <div class="address-detail">
              <p>龙华区民治街道向南新村一区32栋</p>
              <p>18512600311</p>
              <span class="address-edit">修改</span>
            </div>
            <div class="triangle"></div>
          </li>
        </ul>
      </div>
      <div class="cart-item box">
        <div class="cart-item-head">
          <ul>
            <li>商品信息</li>
            <li>规格</li>
            <li>单价</li>
            <li>数量</li>
            <li>金额</li>
          </ul>
        </div>
        <ul class="cart-item-list">
          <!--小屏下-->
          <li v-for="(item,index) in productList" class="phone-cart">
            <div class="cart-message">
              <div class="cart-item-check">
                <a href="javascript:void 0" class="item-check-btn" :class="{check:item.checked}" @click="checkProduct(item)">
                </a>
              </div>
              <div class="cart-item-pic">
                <img :src="item.imgPic" :alt="item.name">
              </div>
              <div class="fl product-name">
                <div class="item-name">{{item.name}}</div>
              </div>
              <div class="fl product-gift">
                <dl>
                  <dd v-for="gift in item.gifts" class="fl">{{gift.partName}}</dd>
                </dl>
              </div>
              <div class="item-price fl">{{item.price | money}}</div>
            </div>
            <div class="cart-fun">
              <div class="cart-tab">
                <div class="item-quantity">
                  <div class="select-self select-self-open">
                    <div class="quantity">
                     <span>数量：</span> {{item.count}}
                    </div>
                  </div>
                </div>
              </div>
              <div class="cart-tab">
                <div><span>规格：</span>11</div>
              </div>
              <div class="cart-tab">
                <div class="item-price-total"><span>总额：</span>{{item.price*item.count | money}}</div>
              </div>
            </div>
          </li>
          <li class="pc-cart" v-for="(item,index) in productList">
            <div class="cart-tab-1">
              <div class="cart-item-check">
                <a href="javascript:void 0" class="item-check-btn" :class="{check:item.checked}" @click="checkProduct(item)">
                </a>
              </div>
              <div class="cart-item-pic">
                <img :src="item.imgPic" :alt="item.name">
              </div>
              <div class="cart-item-title">
                <div class="item-name">{{item.name}}</div>
              </div>
              <div class="item-include">
                <dl>
                  <dt>赠送:</dt>
                  <dd v-for="gift in item.gifts">{{gift.partName}}</dd>
                </dl>
              </div>
            </div>
            <div class="cart-tab-2">
              <div>11</div>
            </div>
            <div class="cart-tab-2">
              <div class="item-price">{{item.price | money}}</div>
            </div>
            <div class="cart-tab-2">
              <div class="item-quantity">
                <div class="select-self select-self-open">
                  <div class="quantity">
                    {{item.count}}
                  </div>
                </div>
              </div>
            </div>
            <div class="cart-tab-2">
              <div class="item-price-total">{{item.price*item.count | money}}</div>
            </div>
          </li>
        </ul>
      </div>
      <div class="order-detail-box clearFloat">
        <div class="order-price-address">
          <ul>
            <li>
              <span>合计：</span>
              <span class="price-sm">¥ 100.00</span>
            </li>
            <li>
              <span>运费：</span>
              <span class="price-sm">¥ 10.00</span>
            </li>
            <li>
              <span class="span-xl">实付款：</span>
              <span class="price-xl">¥ 110.00</span>
            </li>
            <li>
              <span>寄送至：</span>
              广东省深圳市龙华区民治街道
            </li>
            <li>
              <span>收货人：</span>
              朱俊业 18512600311
            </li>
          </ul>
        </div>
        <div class="order-btn">
          <router-link to="/shopCart">返回购物车</router-link>
          <div class="submit-ben">
            提交订单
          </div>
        </div>
      </div>
    </div>
    <productFooter></productFooter>
    <goTop></goTop>
  </div>
</template>

<script type="text/ecmascript-6">
  import productHeader from './header'
  import productFooter from './footer'
  import goTop from './goTop'
  export default {
    components: {
      productHeader,
      productFooter,
      goTop,
    },
    data() {
      return {
        productList: [],//数据
        formShow: false,
        addressShow: true,
        dialogFormVisible: false,
        province: [],
        city: [],
        block: [],
        provinceValue: '',
        cityArr: [],
        cityValue: '',
        areaValue: '',
        areaArr: [],
        selectCountry: [{"country":"中国",id:1,url:'/shopCart'},{"country":"美国",id:2,url:'/productList'}],
        formInline: {
          name: '',
          phone: '',
          country: '中国',
          city: '',
          address: '',
          email: '',
          postCode: ''
        },
        rules: {
          name: [
            {
              required: true,
              message: "请填写姓名",
              trigger: "blur"
            }
          ],
          phone: [
            {
              required: true,
              message: "手机号码不能为空",
            },
            {
              type: "number",
              message: "请填写正确的手机号码"
            }
          ],
          city: [
            {
              required: true,
              message: "请选择地址"
            }
          ],
          address: [
            {
              required: true,
              trigger: "blur",
              message: "请填写详细地址"
            }
          ],
          email: [
            {
              required: true,
              trigger: "blur",
              type: "email",
              message: "请填写正确的email"
            }
          ],
          postCode: [
            {
              required: true,
              trigger: "blur",
              message: "请填写邮编"
            }
          ],
        }
      }
    },
    mounted () {
      this.cartView();
    },
    methods: {
      //数据存放
      cartView: function () {
        let _this = this;
        this.$fetch('http://localhost:8080/static/data.json')
          .then((response) => {
            _this.productList = response.result.list;
          })
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert(this.formInline.name);
          } else {
            // console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.cityArr = [];
        this.cityValue = "";
        this.areaValue = "";
        this.areaArr = [];
        this.$refs[formName].resetFields();
      },
      // 加载china地点数据，三级
      getCityData:function(){
        let that = this;
        this.$fetch('http://localhost:8080/static/city.json').then(function(response){
          // 省市区数据分类
          for (let item in response) {
            if (item.match(/0000$/)) {//省
              that.province.push({id: item, value: response[item], children: []})
            } else if (item.match(/00$/)) {//市
              that.city.push({id: item, value: response[item], children: []});
            } else {//区
              that.block.push({id: item, value: response[item]})
            }
          }
          // 分类市级
          for (let index in that.province) {
            for (let index1 in that.city) {
              if (that.province[index].id.slice(0, 2) === that.city[index1].id.slice(0, 2)) {
                that.province[index].children.push(that.city[index1])
              }
            }
          }
          // 分类区级
          for(let item1 in that.city) {
            for(let item2 in that.block) {
              if (that.block[item2].id.slice(0, 4) === that.city[item1].id.slice(0, 4)) {
                that.city[item1].children.push(that.block[item2])
              }
            }
          }
        })
      },
      // 选省
      choseProvince:function(e) {
        for (let index2 in this.province) {
          if (e === this.province[index2].id) {
            this.provinceValue = this.province[index2].value;//省的值
            this.cityArr = this.province[index2].children;
            this.cityValue = this.province[index2].children[0].value;//市的值
            this.areaArr =this.province[index2].children[0].children;
            this.areaValue = this.province[index2].children[0].children[0].value;//区的值
          }
        }
      },
      // 选市
      choseCity:function(e) {
        for (let index3 in this.city) {
          if (e === this.city[index3].id) {
            this.cityValue = this.city[index3].value;//市的值
            this.areaArr = this.city[index3].children;
            this.areaValue = this.city[index3].children[0].value//区的值
          }
        }
      },
      // 选区
      choseBlock:function(e) {
        let _this = this;
        this.areaArr.forEach(function (item) {
          if (e === item.id){
            _this.areaValue = item.value
          }
        }) ;
      },
    },
    created:function(){
      this.getCityData()
    }
  }
</script>

<style lang="stylus" scoped rel="stylesheet/stylus">
  @import "../common/stylus/general.styl"
  *{transition: all .1s ease-in;}
  .address-li
    float left
    width 22%
    height auto
    border 1px solid #ddd
    position relative
    margin-bottom .3rem
    margin-right 2%
    .address-ss
      margin 0 .1rem
      padding .2rem 0
      border-bottom 1px solid #ddd
      font-size fm
      color #333
      padding-right .5rem
      .address-default
        position absolute
        right 0
        top 0
        background color
        color #fff
        font-size .15rem
        padding .05rem .1rem
  .address-li:nth-child(4n)
    clear right
  .address-detail
    padding .2rem .1rem
    p
      margin-bottom .2rem
      color #333
      font-size fs
  .address-edit
    font-size fs
    color color
    cursor pointer
  .triangle
    content: "";
    position: absolute;
    bottom: 0;
    right: 0;
    border-bottom: 22px solid color;
    border-left: 22px solid transparent;
  .triangle:after
    content ""
    display block
    border 0 solid #fff
    border-width 0 0 1px 1px
    transform rotate(-45deg)
    position absolute !important
    top 11px
    right 2px
    height 4px
    width 8px
  .country-a
    position absolute !important
    width 100%
    left 20px
    top 0
  .select-list
    display flex
    .el-select
      width 100%
      flex 1
  .demo-ruleForm
    width 60%
    margin 0
  .cart-header,.phone-cart
    display none !important
  .shop-cart-wrap
    margin-top 0.25rem
    margin-bottom 0.25rem
  .cart-item
    display table
    width 100%
    border 1px solid #ddd
    box-sizing border-box
  .cart-item-head
    display table-header-group;
    width 100%
  .cart-item-head ul
    display table-row
    width 100%
  .cart-item-head li
    display table-cell
    height 0.3rem
    line-height .9rem
    background #ddd
    color #666
    font-size fm
    text-align center
  .cart-item-head li:nth-child(2), .cart-item-head li:nth-child(3), .cart-item-head li:nth-child(4), .cart-item-head li:nth-child(5)
    width 14%
    padding 0 10px
  .cart-item-list
    display table-row-group
  .cart-item-list > li
    position relative
    display table-row
    padding .5rem 0
    border-bottom 1px solid #ddd
  .cart-item-list > li:last-child
    border none
  .cart-item-list > li > div
    position relative
    display table-cell
    text-align center
    vertical-align top
  .cart-item-list .cart-tab-1
    min-height 1rem
    padding .5rem 0
    text-align left
  .cart-item-list .cart-tab-2
    padding-top 74px
  .cart-item-list .cart-item-check
    float left
    padding 28px 0 28px 22px
  .cart-item-list .cart-item-pic
    float left
    width 100px
    height 100px
    margin-left 22px
  .cart-item-list .cart-item-pic img
    width 100%
    height 100%
    cursor:pointer
  .cart-item-list .cart-item-title
    min-height 68px
    padding 0 20px 0 170px
  .cart-item-list .cart-item-title .item-name
    margin 2px 0 10px
    line-height 16px
    color #000
    font-size 14px
  .cart-item-list .item-include
    position relative
    padding-left 170px
  .cart-item-list .item-include dl dt
    float left
  .cart-item-list .item-include dl dd
    padding-left 40px
    color #666
  .cart-item-list .item-include dl:after
    visibility hidden
    display block
    content " "
    clear both
  .item-price,.item-price-total
    color color
    font-size fm
  .order-price-address
    margin-top .2rem !important
    background #eeeeee
    float right
    padding .3rem
    padding-bottom 0
    li
      font-size fs
      color #666
      text-align right
      margin-bottom .2rem
  .price-sm
    color color
  .span-xl
    font-size fl
  .price-xl
    font-size fl
    color color
  .order-btn
    float right
    width 100%
    text-align right
    margin-top .3rem
    div
      display inline-block
      padding .2rem .4rem
      color #fff
      font-size fm
      border-radius 2px
      background color
      cursor pointer
    a
      display inline-block
      font-size fm
      color #666
      padding .2rem .3rem
      background #eee
      margin-right .2rem
  /* 小于767px */
  @media screen and (max-width: 767px){
    .container{
      width 100%
      padding-bottom 2.6rem
    }
    .cart-item-head{
      display none !important
    }
    .box{
      display block !important
    }
    .cart-header{
      display block !important;
      font-size fxl
      color #fff
      background color
      line-height 1.2rem
      text-align center
    }
    .selected-num,.pc-cart{
      display none !important
    }
    .ls-hide{
      position: absolute;
      bottom: -0.1rem;
      right: 0;
      height: 20px;
      line-height: 20px;
      font-size fs
    }
    .cart-foot-wrap .next-btn-wrap{
      width 2rem
      height 1.3rem
      line-height 1.3rem
    }
    .cart-foot-wrap .cart-foot-l{
      width auto
    }
    .cart-foot-wrap .item-total{
      margin-right .2rem
      position relative
      line-height 1.1rem
      font-size fxl
    }
    .cart-foot-wrap{
      height 1.3rem
      line-height 1.3rem
      position fixed
      bottom 1.3rem
      left 0
      right 0
    }
    .cart-item-list{
      display block
      margin-top 0.2rem
    }
    .cart-item-list > li,.cart-item-list > li > div{
      display block
      overflow hidden
    }
    .cart-item-check{
      margin-top 1.2rem
      margin-left 5%
      padding 0 !important
    }
    .cart-item-list .cart-item-pic{
      width 3rem
      height 3rem
      margin-left 5%
      margin-right .2rem
    }
    .product-name,.product-gift{
      width 52%
      text-align left
      font-size fxl
      margin-bottom  .2rem
    }
    .product-gift dd{
      color #999
      font-size fs
    }
    .item-price{
      font-size fxl
    }
    .cart-fun{
      height 28px
      line-height 28px
      margin-top .5rem
      margin-left 5%
    }
    .cart-tab{
      float left
      margin-right .2rem
    }
    .cart-item{
      border none
    }
    .el-message-box{
      width 70% !important
    }
    .phone-cart{
      display block !important
    }
    .item-price-total,.total-price{
      font-size fxxl !important
    }
    .span-xl,.price-xl {
      font-size fxxl
    }
    .order-btn{
      text-align center
    }
    .order-price-address{
      width 100%
    }
    .demo-ruleForm {
      width 95%
      margin-top .3rem !important
    }
    .select-list{
      display block !important
    }
    .select-list .el-select{
      margin-bottom .2rem !important
    }
    .select-list .el-select:last-child {
      margin-bottom 0 !important
    }
    .address-li{
      width 47%
      margin-right 2%
    }
  }
</style>



