<script type="text/ecmascript-6">
  import productHeader from './header'
  import productFooter from './footer'
  import goTop from './goTop'
  import editlist from './edit_list'
  import bigimg from './big_img'
  import swipershow from './swiperShow'
  import imgtextstairs from './img_text_stairs'
 	import globa from "../common/js/alljson.js"

  export default {
    components: {
      productHeader,
      productFooter,
      goTop,
      globa,
      editlist,
      bigimg,
      swipershow,
      imgtextstairs,
      globa
    },
    data() {
      return {
        imgList: [
          {id: 0, url: "1",idView: require('../common/img/img1.jpg')},
          {id: 1, url: '2', idView: require('../common/img/img2.jpg')},
          {id: 2, url: '3', idView: require('../common/img/img3.jpg')}
        ],
        allinfo:globa.jsons.item,
        loading:true,
        imgHeight:'',
      }
    },
    created:function(){
//  	this.$post('../../static/home.json',{		
//  		
//			}).then( (res) => {												
//				
//			})
    },
    //模板编译挂载
    mounted: function () {
      this.loading=false;
      let that = this;
      let img = new Image();
//    img.src = document.querySelector(".banner-img").src;
      let imgScale = img.width/img.height;
      that.imgHeight = document.body.clientWidth/imgScale+'px';
      window.onresize = function temp() {
        // 通过点语法获取img的height属性值
        that.imgHeight = `${that.$refs.imgHeight['0'].height}px`
      }
    },
    methods: {

    }
  }
</script>

<style lang="stylus" scoped rel="stylesheet/stylus">

  @import "../common/stylus/general.styl"
