			function showdata(dom, data) {
				for(var i = 0; i < data.length; i++) {
					dom.append(`<li data_attr="${data[i].zh_pc_id}" style="text-align:center;position: relative;text-overflow: ellipsis;white-space: nowrap;padding:10px 20px;border-bottom:1px solid rgb(89,89,89)">${data[i].zh_pc_title}<ul style="position: absolute;right:0px;top:0;transform: translateX( calc( 100% - 2px ) );display: none;
					background-color: rgba(67,67,67,1);
					color: white;
					border:none;
					box-shadow: 3px 3px 5px #8C8C8C;"></ul></li>`);
					if(data[i].child && data[i].child.length > 0) {
						showdata($("li[data_attr='" + data[i].zh_pc_id + "'] ul"), data[i].child);
					}
				}
			};
			$("#app").on("mouseover", ".div>ul li", function(e) {
				var e = e || window.event;
				e.stopPropagation();
				$(this).siblings("li").find("ul").hide();
				if($(this).children("ul").is(":visible")) {
					$(this).find("ul").hide();
				} else {
					$(this).children("ul").show();
				}
				

			})
			$("#app").on("mousemove", "#swiper_show", function(e) {
				var e = e || window.event;
				e.stopPropagation();
				if($(e.target).parent()[0].nodeName != 'UL' && $(e.target).parent()[0].nodeName != 'LI') {
					$(".div>ul>li ul").hide();
				}

			})

			$("#app").on("click", ".div>ul li", function(e) {
				var e = e || window.event;
				e.stopPropagation();
				$(this).siblings("li").find("ul").hide();
				if($(this).children("ul").is(":visible")) {
