<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	h1,
	h2 {
		font-weight: normal;
	}
	
	ul {
		list-style-type: none;
		padding: 0;
		word-break: break-all;
	}
	
	li {
		display: inline-block;
		margin: 0 10px;
	}
	
	a {
		color: #42b983;
	}
	
	.slide_img {
		width: 100%;
		padding-bottom: 30%;
		background-repeat: no-repeat;
		background-size: 100% 100%;
	}
	
	.swiper-pagination-bullet {
		background-color: white;
		border: 2px solid transparent;
	}
	
	.swiper-pagination-bullet.swiper-pagination-bullet-active {
		background-color: transparent;
		border-color: 2px solid rgb(66, 167, 73);
	}
	
	#allListNav {
		height: 100%;
		-webkit-overflow-scrolling: touch;
		position: absolute;
		left: 50%;
		top: 0;
		overflow-y: auto;
		width: 60%;
		z-index: 1000;
		transform: translateX(-50%);
		font-size: 14px;
	}
	
	#allListNav::-webkit-scrollbar {
		display: none;
	}
	/*导航*/
	
	.div {
		width: 201px;
		;
		max-height: 100%;
		position: absolute;
		left: 0;
		top: 0;
		z-index: 999;
	}
	
	.div>div {
		width: 100%;
		padding: 10px 20px;
		background-color: rgb(66, 167, 73);
		color: white;
		box-sizing: border-box;
		text-align: center;
	}
	
	.div::-webkit-scrollbar {
		display: none;
	}
	
	.div ul {
		display: none;
		background-color: rgba(67, 67, 67, 1);
		color: white;
		border: none;
		box-shadow: 3px 3px 5px #8C8C8C;
	}
	
	.div ul li {
		text-overflow: ellipsis;
		white-space: nowrap;
		padding: 10px 20px;
	}
	
	.div ul li:hover {
		background: rgb(77, 77, 77);
	}
	
	.div>ul {
		display: block;
	}
	
	.div>ul li:hover>ul {
		display: block;
	}
	
	.div li {
		position: relative;
	}
	
	.div>ul>li ul {
		position: absolute;
		right: 0px;
		top: 0;
		transform: translateX( calc( 100% - 2px));
	}
	
	#swiper_show {
		position: relative;
	}
	
	@media screen and (max-width: 767px) {
		.div {
			/*display: none;*/
			width: 1rem;
		}
	}
</style>
<style>
	#swiper_show .swiper-pagination-bullet {
		background-color: white;
		opacity: 1;
		border: none;
		margin: 2px 6px;
	}
	
	#swiper_show .swiper-pagination-bullet.swiper-pagination-bullet-active {
		background-color: transparent;
		border: 2px solid rgb(66, 167, 73);
		margin: 0px 4px;
	}
</style>


